@import '../../../variables.scss';

.card {
    border: 1px solid $border-color-grey;
    background-color: $background-white;
    margin-bottom: 3.5em;
    border-radius: 3px;

    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.75em;

        .profile {
            margin-bottom: 0;
        }

        .cardButton {
            height: 30px;

            &:hover{
                cursor: pointer;
            }
        }
    }

    .timePosted {
        font-size: 0.6em;
        padding-left: 2em;
        padding-bottom: 1em;
        color: $font-color-secondary;
        border-bottom: 1px solid $border-color-grey-secondary;
    }

    .findDriveBox {
        display: flex;
        justify-content: space-between;
        padding:0 3em;
        font-size: 1.5em;
        color: $font-color-primary;
        
    }
    .timeBox{
        display: flex;
        justify-content: left;
        padding: 0 2em 0.5em;
        align-items: center;

        .clock {
            height: 35px;
            width: 35px;
            padding-right: 1em;
        }
        .cost {
            height: 35px;
            width: 35px;
            padding-right: 1em;
            margin-left: 5em;
        }
    }
    .note {
        padding: 0 1em 0.5em;
    }

}

@media only screen and (max-width: 640px){
    .card {
        border: none;
        background-color: $background-grey;
        margin-bottom: 0.75em;

        .addComment {
            display: none;
        }
        .timePosted {
            border: none;
        }
    }
}